import React,{useEffect} from 'react';
import './ScrollDownIndicator.component.scss';
import { motion, useAnimation } from 'framer-motion'

export default function ScrollDownIndicator() {

  const animateScrollIndicator = useAnimation()

  useEffect(() => {
    animateScrollIndicator.set({
      opacity: [0, 0, 0],
      y: -30
    })
    animateScrollIndicator.start({
      opacity: [0,0,1],
      y: 0,
      transition: {repeat: Infinity, duration: 2 }
    })
  }, [animateScrollIndicator]);
  
  return (
      <motion.div className="scroll-down-indicator" animate={animateScrollIndicator}>
        <svg width="64" height="64" viewBox="0 0 64 64" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="32" cy="32" r="30" stroke="#1DCAD3" stroke-width="4"/>
            <path d="M23 37L32 44M32 44L41 37M32 44V21" stroke="#1DCAD3" stroke-width="4" stroke-linecap="round" stroke-linejoin="round"/>
        </svg>
      </motion.div>
  );
}
