import React,{useContext} from 'react';
import './portfolioItem.component.scss';
import LayoutPage from '../../components/layoutPage/layoutPage.component.jsx';
import ScrollDownIndicator from '../../components/ScrollDownIndicator/ScrollDownIndicator.component.jsx';

import PortfolioImage from '../../assets/images/cybersecurity.webp';
// import DummyIcon from '../../assets/icons/iconManagedIT.svg'
// import SectionH1 from '../../components/sectionH1/sectionh1.component.jsx';
import SectionH2 from '../../components/sectionH2/sectionh2.component.jsx';
import Paragraph from '../../components/paragraph/paragraph.component.jsx';
// import Icon from '../../components/icon/icon.component.jsx';
import portfolioPageData from '../../data/portfolioPageData.json';
import { useLocation } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import { AppContext } from '../../AppContext';

export default function PortfolioItem(props) {
  let location = useLocation();
  const pathname = location.pathname;
  const { isLG } = useContext(AppContext);

  return portfolioPageData.map((data) => {
    if (`/${data.link}` === pathname) {
      return (
        <LayoutPage>
          <Helmet>
            <title>{data.title}</title>
            <meta
              name="description"
              content={`ISR Technology - USA, MIAMI, FL. ${data.paragraph}`}
            />
          </Helmet>
          <div className="service-image-socket" id="service-image-socket">
            <div className="overlay"></div>
            <img
              src={PortfolioImage}
              alt={data.title}
              className="portfolio-image"
            />
            { isLG && <ScrollDownIndicator/>}
          </div>
          <div className="service-page-content" id="service-page-content">
            <div className="service-page-header container">
              <SectionH2 h2={data.title} />
            </div>
            <div className="service-page-body container">
              <Paragraph paragraph={data.paragraph} />
            </div>
          </div>
        </LayoutPage>
      );
    }
  });
}
