import React, {useState, useEffect} from 'react';
import './Carousel.component.scss';
import {images} from '../../data/AboutCarouselImages.js';
import { motion, AnimatePresence } from 'framer-motion';
import ScrollDownIndicator from '../../components/ScrollDownIndicator/ScrollDownIndicator.component.jsx';

export default function Carousel() {
    const [[page, direction], setPage] = useState([0,0]);
    const [imageIndex, setImageIndex] = useState(0);
    const [link, setLink] = useState(images[0].link)

    console.log(images.length)
    console.log("index", imageIndex)

    const paginate = newDirection => {
        setPage([page + newDirection, newDirection])
        setImageIndex(imageIndex + newDirection)
        console.log("set link")
        if (imageIndex === images.length - 1) {
            setImageIndex(0)
            setLink(images[0].link)
        } else {
            setLink(images[imageIndex + newDirection].link)
        }
    }

    useEffect(() => {
        const next = document.getElementById("next");
        const interval = setInterval(() => next.click(), 5000);
        return () => {
            clearInterval(interval);
          };
    }, []);

    
    
    return (
        <div className='carousel full-vh'>
            <div className="overlay"></div>
            <AnimatePresence initial={false} custom={direction}>
                <motion.img 
                    key={page}
                    custom={direction}
                    className='carousel-image'
                    src={images[imageIndex].link}
                    initial={{opacity: 0}}
                    animate={{opacity: 1}}
                    exit={{opacity: 0}}
                    transition={{
                        opacity: { duration: 1 }
                    }}
                />
            </AnimatePresence>
            <div 
            className='next'
            onClick={() => paginate(1)}
            id="next"
            transition={{
                opacity: { duration: 1 }
            }}
            >
                <p>+</p>
            </div>
            <div 
            className='prev'
            onClick={() => paginate(-1)}
            >
                <p>-</p>
            </div>
            <div className="carousel-content container">
  
                <div className='carousel-text'>
                    <h1 className='display2'>ready for future</h1>
                </div>
    
            </div>
            <div className="carousel-bottom-info">
                <ScrollDownIndicator/>
                <div className='carousel-bottom-data'>
                    <p className="caption">{images[imageIndex].service}</p>
                    <p className="caption"><span>{images[imageIndex].id}</span> / {images.length}</p>
                </div>
            </div>
        </div>
    );
}
